import React, { useEffect } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';
import { useSignUpUserMutation } from '../redux';

const SignupSchema = Yup.object().shape({
  name: Yup.string()
    .min(4, 'Name must contain 4 to 20 characters!')
    .max(20, 'Name must contain 4 to 20 characters!')
    .required('Required'),
  email: Yup.string().email('Invalid email').required('Required'),
  password: Yup.string()
    .min(7, 'Password must contain 7 or more characters!')
    .required('Required'),
});

export default function Register() {
  const navigate = useNavigate();
  const [signUpUser, { isSuccess }] = useSignUpUserMutation();

  useEffect(() => {
    if (isSuccess) {
      navigate('/contacts');
    }
  }, [isSuccess, navigate]);

  return (
    <div className="bg-white dark:bg-gray-900">
      <div className="flex justify-center h-screen">
        <div
          className="hidden bg-cover lg:block lg:w-2/3"
          style={{
            backgroundImage:
              'url(https://images.unsplash.com/photo-1616763355603-9755a640a287?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1470&q=80)',
          }}
        >
          <div className="flex items-center h-full px-20 bg-gray-900 bg-opacity-40">
            <div>
              <h2 className="text-4xl font-bold text-white">
                Your cloud phonebook
              </h2>

              <p className="max-w-xl mt-3 text-gray-300">
                Store and edit all your contatacts in cloud!
              </p>
            </div>
          </div>
        </div>

        <div className="flex items-center w-full max-w-md px-6 mx-auto lg:w-2/6">
          <div className="flex-1">
            <div className="text-center">
              <h2 className="text-4xl font-bold text-center text-gray-700 dark:text-white">
                Your cloud phonebook
              </h2>

              <p className="mt-3 text-gray-500 dark:text-gray-300">
                Sign up to create your account
              </p>
            </div>

            <div className="mt-8">
              <Formik
                initialValues={{
                  name: '',
                  email: '',
                  password: '',
                }}
                validationSchema={SignupSchema}
                onSubmit={(values, { resetForm }) => {
                  const { name, email, password } = values;
                  signUpUser({ name, email, password });
                  resetForm();
                }}
              >
                {({ errors, touched }) => (
                  <Form>
                    <div className="mt-6">
                      <label
                        htmlFor="name"
                        className="block mb-2 text-sm text-gray-600 dark:text-gray-200"
                      >
                        Name
                      </label>
                      <Field
                        id="name"
                        name="name"
                        type="name"
                        placeholder="Your name"
                        autoComplete="off"
                        autoFocus
                        className="block w-full px-4 py-2 mt-2 text-gray-700 placeholder-gray-400 bg-white border border-gray-200 rounded-md dark:placeholder-gray-600 dark:bg-gray-900 dark:text-gray-300 dark:border-gray-700 focus:border-blue-400 dark:focus:border-blue-400 focus:ring-blue-400 focus:outline-none focus:ring focus:ring-opacity-40"
                      />
                      {errors.name && touched.name ? (
                        <div className="block mb-2 text-sm text-red-600 dark:text-red-200">
                          {errors.name}
                        </div>
                      ) : null}
                    </div>
                    <div className="mt-6">
                      <label
                        htmlFor="email"
                        className="block mb-2 text-sm text-gray-600 dark:text-gray-200"
                      >
                        Email Address
                      </label>
                      <Field
                        id="email"
                        name="email"
                        type="email"
                        placeholder="example@example.com"
                        autoComplete="off"
                        className="block w-full px-4 py-2 mt-2 text-gray-700 placeholder-gray-400 bg-white border border-gray-200 rounded-md dark:placeholder-gray-600 dark:bg-gray-900 dark:text-gray-300 dark:border-gray-700 focus:border-blue-400 dark:focus:border-blue-400 focus:ring-blue-400 focus:outline-none focus:ring focus:ring-opacity-40"
                      />
                      {errors.email && touched.email ? (
                        <div className="block mb-2 text-sm text-red-600 dark:text-red-200">
                          {errors.email}
                        </div>
                      ) : null}
                    </div>
                    <div className="mt-6">
                      <div className="flex justify-between mb-2">
                        <label
                          htmlFor="password"
                          className="text-sm text-gray-600 dark:text-gray-200"
                        >
                          Password
                        </label>
                      </div>
                      <Field
                        type="password"
                        name="password"
                        id="password"
                        placeholder="Your Password"
                        autoComplete="off"
                        className="block w-full px-4 py-2 mt-2 text-gray-700 placeholder-gray-400 bg-white border border-gray-200 rounded-md dark:placeholder-gray-600 dark:bg-gray-900 dark:text-gray-300 dark:border-gray-700 focus:border-blue-400 dark:focus:border-blue-400 focus:ring-blue-400 focus:outline-none focus:ring focus:ring-opacity-40"
                      />
                      {errors.password && touched.password ? (
                        <div className="block mb-2 text-sm text-red-600 dark:text-red-200">
                          {errors.password}
                        </div>
                      ) : null}
                    </div>

                    <div className="mt-6">
                      <button
                        type="submit"
                        className="w-full px-4 py-2 tracking-wide text-white transition-colors duration-200 transform bg-blue-500 rounded-md hover:bg-blue-400 focus:outline-none focus:bg-blue-400 focus:ring focus:ring-blue-300 focus:ring-opacity-50"
                      >
                        Sign up
                      </button>
                    </div>
                  </Form>
                )}
              </Formik>

              <p className="mt-6 text-sm text-center text-gray-400">
                Already have an account?{' '}
                <NavLink
                  to="/login"
                  className="text-blue-500 focus:outline-none focus:underline hover:underline"
                >
                  Sign in
                </NavLink>
                .
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
